<template>
<div class="home">
  <form action="#" class="search" @submit.prevent="loadSearch($event)">
    <input type="text" placeholder="Поиск по названию" class="search-input">
    <button type="submit" class="search-btn">
      <svg version="1.1" id="Capa_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="30.239px" height="30.239px" viewBox="0 0 30.239 30.239" style="enable-background:new 0 0 30.239 30.239" xml:space="preserve">
        <path d="M20.194,3.46c-4.613-4.613-12.121-4.613-16.734,0c-4.612,4.614-4.612,12.121,0,16.735 c4.108,4.107,10.506,4.547,15.116,1.34c0.097,0.459,0.319,0.897, 0.676,1.254l6.718,6.718c0.979,0.977,2.561,0.977,3.535,0 c0.978-0.978,0.978-2.56,0-3.535l-6.718-6.72c-0.355-0.354-0.794-0.577-1.253-0.674C24.743,13.967,24.303,7.57,20.194,3.46z M18.073,18.074c-3.444,3.444-9.049,3.444-12.492,0c-3.442-3.444-3.442-9.048,0-12.492c3.443-3.443,9.048-3.443,12.492,0 C21.517,9.026,21.517,14.63,18.073,18.074z"/>
      </svg>
    </button>
  </form>

  <div class="main">
    <div class="beers-search" v-if="search">
      <div class="beers-search__text">Результат поиска по запросу "{{ searchReq }}"</div>
      <button class="beers-search__btn" @click.prevent="reset()">Назад</button>
    </div>
    <div class="beers">
      <div class="beer" v-for="beer in beerItems" :key="beer.id">
        <div class="beer-img"><img :src="beer.image_url" alt="Beer image"></div>
        <div class="beer-text">
          <div class="beer-text__title">{{ beer.name }}</div>
          <a href="#" class="beer-text__btn" @click.prevent="toggleFavourite(beer)">
            <img src="../assets/heart_active.svg" alt="Heart image" v-if="beer.favourite == true">
            <img src="../assets/heart.svg" alt="Heart image" v-else>
          </a>
          <div class="beer-text__descr">{{ beer.description }}</div>
        </div>
      </div>
    </div>
    
    <div class="pagination" v-if="pagination">
      <a href="#" class="pagination-item pagination-before" v-if="page > 1" @click.prevent="loadPage(page - 1)">&lt;</a>
      <div class="pagination-item pagination-current">{{ page }}</div>
      <a href="#" class="pagination-item pagination-after" @click.prevent="loadPage(page + 1)">&gt;</a>
    </div>
  </div>
</div>
</template>

<script>
export default {
  data() {
    return {
      pagination: true,
      searchReq: '',
      search: false
    }
  },
  methods: {
    loadPage(neededPage) {
      this.$store.dispatch('LOAD_BEERS_PAGE', neededPage)
      window.scrollTo(0, 0)
    },
    loadSearch(event) {
      const search = document.querySelector('.search-input')
      if (search.value != '') {
        this.pagination = false
        this.search = true
        this.$store.dispatch('LOAD_BEERS_SEARCH', this.searchReq)
        this.searchReq = search.value
      }
      event.target.reset()
    },
    reset() {
      this.pagination = true
      this.search = false
      this.$store.dispatch('LOAD_BEERS_PAGE', 1)
      this.searchReq = ''
    },
    toggleFavourite(beer) {
      for (let i = 0; i < this.favourites.length; i++) {
        if (this.favourites[i].id == beer.id) {
          this.$store.dispatch('REMOVE_FAVOURITE', beer)
          // console.log('component remove: ', this.favourites)
          return
        }
      }
      this.$store.dispatch('ADD_FAVOURITE', beer)
      // console.log('component add: ', this.favourites)
    }
  },
  computed: {
    beerItems() {
      return this.$store.getters.beerPageItems
    },
    favourites() {
      return this.$store.getters.favourites
    },
    page() {
      return this.$store.getters.page
    }
  }
}
</script>

