import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    beerItems: [],
    beerPageItems: [],
    page: 1
  },
  mutations: {
    ADD_BEERS(state, payload) {
      const beers = payload.beers
      const oldPage = state.page
      state.page = payload.page
      state.beerPageItems = []
      const beersLength = state.beerItems.length
      if (oldPage <= payload.page)
        for (let i = 0; i < beers.length; i++) {
          Vue.set(state.beerPageItems, i, beers[i])
          Vue.set(state.beerPageItems[i], state.beerPageItems[i].favourite, false)
          Vue.set(state.beerItems, beersLength + i, beers[i])
          Vue.set(state.beerItems[beersLength + i], state.beerItems[beersLength + i].favourite, false)
        }
      else
        for (let i = (payload.page - 1) * 24; i < payload.page * 24; i++)
          Vue.set(state.beerPageItems, i - ((payload.page - 1) * 24), state.beerItems[i])
    },
    ADD_FAVOURITE(state, beer) {
      for (let i = 0; i < state.beerPageItems.length; i++) {
        if (state.beerPageItems[i].id == beer.id) {
          Vue.set(state.beerPageItems[i], state.beerPageItems[i].favourite, true)
          state.beerPageItems[i].favourite = true
          break
        }
      }
      beer.favourite = true
      let newBeerPageItems = state.beerPageItems
      let newBeerItems = state.beerItems
      state.beerPageItems = []
      state.beerItems = []
      for (let i = 0; i < newBeerPageItems.length; i++)
        Vue.set(state.beerPageItems, i, newBeerPageItems[i])
      for (let i = 0; i < newBeerItems.length; i++)
        Vue.set(state.beerItems, i, newBeerItems[i])
      // console.log('vuex add: ', state.beerPageItems.filter(i => i.favourite == true))
    },
    REMOVE_FAVOURITE(state, beer) {
      for (let i = 0; i < state.beerPageItems.length; i++) {
        if (state.beerPageItems[i].id == beer.id) {
          Vue.set(state.beerPageItems[i], state.beerPageItems[i].favourite, false)
          state.beerPageItems[i].favourite = false
          break
        }
      }
      beer.favourite = false
      let newBeerPageItems = state.beerPageItems
      let newBeerItems = state.beerItems
      state.beerPageItems = []
      state.beerItems = []
      for (let i = 0; i < newBeerPageItems.length; i++)
        Vue.set(state.beerPageItems, i, newBeerPageItems[i])
      for (let i = 0; i < newBeerItems.length; i++)
        Vue.set(state.beerItems, i, newBeerItems[i])
      // console.log('vuex removed: ', state.beerPageItems.filter(i => i.id == beer.id))
      // console.log('vuex remove: ', state.beerPageItems.filter(i => i.favourite == true))
    }
  },
  actions: {
    LOAD_BEERS_PAGE({commit}, page) {
      const requestStr = `https://api.punkapi.com/v2/beers?page=${page}&per_page=24`
      get(requestStr)
      .then((res) => {
        const beers = JSON.parse(res)
        commit('ADD_BEERS', { beers: beers, page: page })
      })
      .catch((err) => {
        console.log(err)
      })
    },
    LOAD_BEERS_SEARCH({commit}, beerName) {
      const requestStr = `https://api.punkapi.com/v2/beers?beer_name=${beerName.toLowerCase().split(' ').join('_')}`
      get(requestStr)
      .then((res) => {
        const beers = JSON.parse(res)
        commit('ADD_BEERS', beers)
      })
      .catch((err) => {
        console.log(err)
      })
    },
    ADD_FAVOURITE({commit}, beer) {
      commit('ADD_FAVOURITE', beer)
    },
    REMOVE_FAVOURITE({commit}, beerIndex) {
      commit('REMOVE_FAVOURITE', beerIndex)
    }
  },
  getters: {
    beerPageItems: (state) => state.beerPageItems,
    favourites: (state) => state.beerItems.filter(i => i.favourite == true),
    page: (state) => state.page
  }
})

function get(url) {
  return new Promise(function(succeed, fail) {
    const request = new XMLHttpRequest()
    request.open("GET", url, true)
    request.addEventListener("load", function() {
      if (request.status < 400)
        succeed(request.response)
      else
        fail(new Error("Request failed: " + request.statusText))
    })
    request.addEventListener("error", function() {
      fail(new Error("Network error"))
    })
    request.send()
  })
}